import Vue from 'vue'
import Router from 'vue-router'
import Hello from '@/components/Hello'
import Find from '@/components/Find'
import Shou from '@/components/Shou'
import Mime from '@/components/Mime'

Vue.use(Router)

export default new Router({
  routes: [
  {
  	path:'/',
  	redirect:'/hello'
  },
    {
      path: '/hello',
      name: 'Hello',
      component: Hello
    },
     {
      path: '/find',
      name: 'Find',
      component: Find
    },
     {
      path: '/shou',
      name: 'Shou',
      component: Shou
    },
     {
      path: '/mime',
      name: 'Mime',
      component: Mime,
       children:[
   	      {path:'/mime/one',component:resolve=>{
   	      	require(['@/hello/one'],resolve)
   	      }},
   	      {path:'/mime/two',component:resolve=>{
   	      	require(['@/hello/two'],resolve)
   	      }},
   	       {path:'/mime/three',component:resolve=>{
   	      	require(['@/hello/three'],resolve)
   	      }},
   	       {path:'/mime/four',component:resolve=>{
   	      	require(['@/hello/four'],resolve)
   	      }}
   	    ]
    }
  ]
})



