<template>
	
	<div class="myhead">
		<span>{{title}}</span>
	</div>
</template>

<script>
export default {
  name: 'myhead',
  props:["title"]
}
</script>

<style>
	.myhead{
		width:100%;
		height:4.8rem;
		background: skyblue;
		position:absolute;
		top:0;
		text-align: center;
	}
	.myhead span{
		font-size:0.16rem;
		line-height:4.8rem;
		
	}
</style>


