<template>
	
	<div class="shou">
		
		<myhead title="首页"/>
		<div class="shou-div">
			  <button @click="show = !show">Toggle show</button>
			  <transition name="bounce">
			    <p v-if="show">Look at me!</p>
			  </transition>
			  
			
             <input type="text" ref="input"/>
             <button @click="find">点我</button>
         </ul>
		</div>
		
		<myfoot/>
	</div>
</template>

<script>
	
	import myhead from '@/common/myhead'
	import myfoot from '@/common/myfoot'
	
	export default {
	  name: 'shou',
	  components:{
	         myhead,myfoot
	  },
	  methods:{
	  	find:function(){
	  		 console.log(this.$refs.input.value)
	  		 this.$refs.input.value=''
	  	}
	  },
	  data(){
	  	return{
	  		show:true,
	  		people:['三姑','四婶','五叔','六姨','七舅姥爷']
	  	}
	  },
	  created: function() {
		 this.$nextTick(() => {
		  console.log(this.$refs);
		 })
    }
}
</script>

<style>
	.shou-div{
		width:100%;
		margin-top:4.8rem;
	}
    .bounce-enter-active {
     animation: bounce-in .5s;
  }
	.bounce-leave-active {
	   animation: bounce-in .5s reverse;
	}
	@keyframes bounce-in {
	  0% {
	    transform: scale(0);
	  }
	  50% {
	    transform: scale(1.5);
	  }
	  100% {
	    transform: scale(1);
	  }
	}
	  
</style>


