<template>
	<div class="find">
		
		<myhead title="发现"/>
			<div id="find-data">
				<div v-for="i in lists">
					<p>{{i.title}}</p>
					<p>{{i.author.loginname}}</p>
					<img :src="i.author.avatar_url"/>
				</div>
				
			</div>
		<myfoot/>
	</div>
</template>

<script>
	import myhead from '@/common/myhead'
	import myfoot from '@/common/myfoot'
	import axios from 'axios'
	
	export default {   
	       name: 'find',
		   components:{
	         myhead,myfoot
	      },
	     data () {
		    return {
		         lists:[]
		    }
		  },
	    mounted(){
		    var self=this;
		    axios({
		      url:"https://cnodejs.org/api/v1/topics?tab=good&mdrender=false&limit=20",
		      headers:{
		        'Content-Type':'application/json'
		      },
		      method:"get"
		    }).then((res)=>{
		    	console.log(res.data.data)
		        self.lists=res.data.data
		    })
	  }
	
}
</script>

<style scoped="">
	
	#find-data{
		width:100%;
		margin-top:4.8rem;
	}
</style>


