<template>
	<div class="mime">
		
		<myhead title="我的"/>
		    <div id="mime-children">
		    	 <ol>
			 	     <li><router-link to="/mime/one">mime-1</router-link></li>
					 <li><router-link to="/mime/two">mime-2</router-link></li>
					 <li><router-link to="/mime/three">mime-3</router-link></li>
					 <li><router-link to="/mime/four">mime-4</router-link></li>
		        </ol>
		        
		    </div>
		    <router-view></router-view>
		    
		<myfoot/>
	</div>
</template>

<script>
	import myhead from '@/common/myhead'
	import myfoot from '@/common/myfoot'
	
	export default {
     name: 'mime',
     components:{
         myhead,myfoot
     }
  }
</script>

<style>
	
	#mime-children{
		width:100%;
		margin-top:4.8rem;
		
	}
	#mime-children ol{
		width:100%;
		height:4rem;
		display: flex;
		text-align: center;
		align-items: center;
		justify-content: space-around;
		background: #f5f5f5;
	}
	#mime-children ol a{
		color:#000;
		text-decoration: none;
		
	}
	#mime-children ol a.router-link-active{
	    color:skyblue;
	}
</style>


